// src/utils/csvMapping.js
import Papa from 'papaparse';
/**
 * CSVID
 * 
 * @param {string} universityName 
 * @returns {string} ID
 */
export const generateId = (universityName) => {
    // undefined 
    if (!universityName) {
        console.warn('ID');
        return `temp-${Date.now()}-${Math.floor(Math.random() * 1000)}`;
    }
    
    // ID
    return universityName
      .replace(/[^a-zA-Z0-9\u3040-\u309F\u30A0-\u30FF\u4E00-\u9FAF]/g, '')
      .toLowerCase();
  };
  
  /**
   * CSV
   * 
   * @param {Object} csvRow CSV1
   * @returns {Object} 
   */
  export const mapCsvToUniversity = (csvRow) => {
    // null
    if (!csvRow) {
        console.warn('CSVnullundefined');
        return null;
    }
    
    console.log('CSV:', csvRow);
    
    // 
    const universityName = csvRow[''] || '';
    const id = generateId(universityName);
    
    // 
    const recommendCriteria = csvRow[''] ? parseFloat(csvRow['']) : null;
    const acceptanceCount = csvRow[''] ? parseInt(csvRow['']) : null;
    
    // J
    const jLeagueTrend = calculateJLeagueTrend(
      csvRow['22J'] ? parseFloat(csvRow['22J']) : 0,
      csvRow['23J'] ? parseFloat(csvRow['23J']) : 0,
      csvRow['24J'] ? parseFloat(csvRow['24J']) : 0
    );
    
    return {
      id,
      university_name: csvRow[''] || '',
      homepage_url: csvRow['HP'] || '',
      main_faculties: parseFaculties(csvRow['']),
      soccer_club: {
        league: csvRow[''] || '',
        coach_name: csvRow[''] || '',
        total_members: parseInt(csvRow['']) || 0,
        members_by_grade: {
          "1": parseFloat(csvRow['']) || 0,
          "2": parseFloat(csvRow['']) || 0,
          "3": parseFloat(csvRow['']) || 0,
          "4": parseFloat(csvRow['']) || 0
        },
        j_league_nominees_2022: parseFloat(csvRow['22J']) || 0,
        j_league_nominees_2023: parseFloat(csvRow['23J']) || 0,
        j_league_nominees_2024: parseFloat(csvRow['24J']) || 0,
        denso_cup_2024_25: parseFloat(csvRow['24']) || 0,
        soccer_field_count: parseFloat(csvRow['']) || 0,
        dorm_available: csvRow[''] === '',
        facility_note: csvRow[''] || '',
        sports_scholarship: false, // CSVfalse
        qualifications: parseQualifications(csvRow['']),
        qualification_note: ''
      },
      entry_conditions: {
        sports_recommend: csvRow[''] === '',
        recommend_criteria: csvRow[''] ? `${csvRow['']}${csvRow['']}` : '',
        selection: csvRow[''] === '',
        selection_period: csvRow[''] || '',
        general_admission: csvRow[''] === '',
        general_conditions: csvRow[''] || ''
      },
      // 
      newMembers2024: csvRow['24'] || '',
      newMembers2025: csvRow['25'] || '',
      jLeagueTrend,
      evaluationGrade: recommendCriteria,
      acceptanceCount: acceptanceCount,
      playmakerComment: csvRow['PLAYMAKER'] || ''
    };
  };
  
  /**
   * 
   * 
   * @param {string} facultiesStr 
   * @returns {Array} 
   */
  function parseFaculties(facultiesStr) {
    if (!facultiesStr) return [];
    
    // 
    return facultiesStr.split(/[,]/).map(f => f.trim()).filter(Boolean);
  }
  
  /**
   * 
   * 
   * @param {string} facultiesStr 
   * @returns {Array} 
   */
  function parseQualifications(facultiesStr) {
    if (!facultiesStr) return [];
    
    const qualifications = [];
    
    // 
    if (/(||)/.test(facultiesStr)) {
      qualifications.push("");
      qualifications.push("JFA");
    }
    
    // 
    if (/(|||)/.test(facultiesStr)) {
      qualifications.push("");
    }
    
    // 
    if (/(||)/.test(facultiesStr)) {
      qualifications.push("");
    }
    
    // 
    qualifications.push("");
    
    return qualifications;
  }
  
  /**
   * J
   * 
   * @param {number} year2022 2022
   * @param {number} year2023 2023
   * @param {number} year2024 2024
   * @returns {string}  ('up'|'down'|'same')
   */
  function calculateJLeagueTrend(year2022, year2023, year2024) {
    // 
    if (year2024 > year2023) {
      return 'up';
    } else if (year2024 < year2023) {
      return 'down';
    } else {
      return 'same';
    }
  }
  
  /**
   * CSV
   * 
   * @param {string} csvContent CSV
   * @returns {Array} 
   */
  export const parseCsvToUniversities = (csvContent) => {
    // PapaParseCSV
    const parsed = Papa.parse(csvContent, {
      header: true,
      skipEmptyLines: true,
      dynamicTyping: true
    });
    
    // 
    return parsed.data.map(row => mapCsvToUniversity(row));
  };
  
  /**
   * 
   * 
   * @param {string} sizeCategory  ('small'|'medium'|'large')
   * @returns {Function} 
   */
  export const filterBySize = (sizeCategory) => {
    return (university) => {
      const memberCount = university.soccer_club.total_members;
      
      switch(sizeCategory) {
        case 'small':
          return memberCount <= 49;
        case 'medium':
          return memberCount >= 50 && memberCount <= 79;
        case 'large':
          return memberCount >= 80;
        default:
          return true;
      }
    };
  };
  
  /**
   * J
   * 
   * @param {number} minCount 
   * @returns {Function} 
   */
  export const filterByJLeagueNominees = (minCount) => {
    return (university) => {
      const totalNominees = 
        (university.soccer_club.j_league_nominees_2022 || 0) + 
        (university.soccer_club.j_league_nominees_2023 || 0) + 
        (university.soccer_club.j_league_nominees_2024 || 0);
      
      return totalNominees >= minCount;
    };
  };
  
  /**
   * J
   * 
   * @param {number} year  (2022|2023|2024)
   * @param {number} minCount 
   * @returns {Function} 
   */
  export const filterByYearJLeagueNominees = (year, minCount) => {
    return (university) => {
      let count = 0;
      
      switch(year) {
        case 2022:
          count = university.soccer_club.j_league_nominees_2022 || 0;
          break;
        case 2023:
          count = university.soccer_club.j_league_nominees_2023 || 0;
          break;
        case 2024:
          count = university.soccer_club.j_league_nominees_2024 || 0;
          break;
        default:
          return true;
      }
      
      return count >= minCount;
    };
  };
  
  /**
   * 
   * 
   * @param {number} maxGrade 
   * @returns {Function} 
   */
  export const filterByGradeRequirement = (maxGrade) => {
    return (university) => {
      // 
      if (!university.evaluationGrade) return false;
      
      return university.evaluationGrade <= maxGrade;
    };
  };